@import "~admin-stylesheets/colors";

.nab-migrate-experiment {

	&__progress-bar-holder {
		background: #eee;
		border-radius: 4px;
		height: 2em;
		overflow: hidden;
		width: 100%;
		margin: 0;
		padding: 0;
	}

	&__progress-bar {
		animation: nab-experiment-migration__animation 2s linear infinite;
		background: $color__experiment-winner;
		background-image: linear-gradient(-45deg, rgba(255, 255, 255, 0.125) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.125) 50%, rgba(255, 255, 255, 0.125) 75%, transparent 75%, transparent);
		background-size: 35px 35px;
		border-color: $color__experiment-winner;
		color: $color__fill-white;
		height: 100%;
		margin: 0;
		padding: 0;
		text-shadow: 1px 1px 1px $color__background_black_translucent;
		transition: width 200ms ease-in-out;

		&--completed {
			animation: none;
			background: $color__experiment-winner;
		}
	}

}

@keyframes nab-experiment-migration__animation {

	0% {
		background-position: 0 0;
	}

	100% {
		background-position: 35px 35px;
	}

}
